<template>
  <div class="users-container">
    <div style="display: flex; justify-content: space-between;">
      <div style="display: flex;">
        <el-input
          style="border-radius: 4px;"
          placeholder="Search"
          prefix-icon="el-icon-search"
          v-model="searchKeyword"
        ></el-input>

        <el-select
          v-model="statusType"
          placeholder="Select"
          style="margin-left: 10px"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <el-button
        style="background: #0f3959; color: white;"
        icon="el-icon-plus"
        @click="openNewComputerDialog"
        >New Computer</el-button
      >
    </div>

    <el-table
      :data="computerList"
      border
      style="width: 100%; margin-top: 10px;"
      :header-cell-style="headerCellStyle"
      @row-click="onClickRow"
    >
      <el-table-column
        sortable
        prop="computerId"
        label="Computer ID"
        width="180"
      >
      </el-table-column>
      <el-table-column
        sortable
        prop="computerName"
        label="Computer Name"
        width="180"
      >
      </el-table-column>
      <el-table-column sortable prop="group" label="Group"> </el-table-column>
      <el-table-column label="Status">
        <template slot-scope="scope">
          {{ scope.row.status ? "Active" : "Deactive" }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      searchKeyword: "",
      options: [
        {
          value: -1,
          label: "Show All",
        },
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
      statusType: -1,
    };
  },
  methods: {
    headerCellStyle() {
      return {
        background: "#0f3959",
        color: "white",
        fontWeight: "normal",
      };
    },
    openNewComputerDialog() {
      this.$store.commit("setNewComputerDialogVisibility", true);
    },
    onClickRow(object) {
      this.$store.commit("computer/setUpdateComputer", object);
      this.$store.commit("setEditComputerDialogVisibility", true);
    },
  },
  computed: {
    ...mapState("computer", ["list"]),
    computerList() {
      let computers = [];
      this.list.forEach((computer) => {
        if (
          (computer.computerId &&
            computer.computerId.toLowerCase().includes(this.searchKeyword)) ||
          (computer.computerName &&
            computer.computerName.toLowerCase().includes(this.searchKeyword)) ||
          computer.group.toLowerCase().includes(this.searchKeyword)
        ) {
          if (this.statusType > -1) {
            if (computer.status == this.statusType) {
              computers.push(computer);
            }
          } else {
            computers.push(computer);
          }
        }
      });
      return computers;
    },
  },
  mounted() {
    this.$store.dispatch("computer/loadList", { notify: this.$notify });
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/users.scss";
</style>
